@import './variables';

.page-content {
    padding-left: 10px;
}
.page-content-wrapper {
    padding-top: 65px;
}

.page-title {
    background-color: #fff;
    position: fixed;
    width: 100%;
    color: #172b4d;
    line-height: 42px;
    font-size: 36px;
    font-weight: 400;
    padding-top: 15px;
    padding-left: 25px;
    padding-bottom: 20px;
    transform: translate(-25px, -15px);
    box-shadow: 0 6px 13px -3px rgba(49, 74, 156, 0.4);
}

.page-sub-title {
    color: #000;
    line-height: 24px;
    font-size: 18px;
    font-weight: 400;
    padding: 15px 0;
}

.page-text {
    color: #000;
    font-size: 14px;
    line-height: 18px;
    padding: 5px 0 19px;
}

@media (max-width: $mediaQueryMobile) {
    .page-title {
        line-height: 30px;
        font-size: 24px;
    }
}
